.custom-select {
  width: 140px;
  height: 32px;
  border-radius: 4px;
  background-color: #e9e9e9;
  font-size: 14px;
  font-weight: 500;
  font-style: normal;
  font-stretch: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #414141;
  border: none;

  option {
    width: 174px;
    height: 33px;
    background-color: #ffffff;
  }
}
