.loader {
	-webkit-animation: spin 1s linear infinite;
	        animation: spin 1s linear infinite;
}

@-webkit-keyframes spin {
	0% {
		-webkit-transform: rotateZ(0);
		        transform: rotateZ(0);
	}
	100% {
		-webkit-transform: rotateZ(360deg);
		        transform: rotateZ(360deg);
	}
}

@keyframes spin {
	0% {
		-webkit-transform: rotateZ(0);
		        transform: rotateZ(0);
	}
	100% {
		-webkit-transform: rotateZ(360deg);
		        transform: rotateZ(360deg);
	}
}

